import React from 'react';
import connect from '@vkontakte/vkui-connect';
import {View} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedData: null,
		};
	}

	componentDidMount() {
		App.loadScript("//cdnjs.cloudflare.com/ajax/libs/zone.js/0.9.1/zone.min.js");
		App.loadScript("//cdnjs.cloudflare.com/ajax/libs/webcomponentsjs/2.2.10/custom-elements-es5-adapter.js", null, true);
		App.loadScript("//static.cloud-clinic.ru/appointment-widget/main-es2015.js", "module");
		App.loadScript("//static.cloud-clinic.ru/appointment-widget/main-es5.js", null, true);

		const base = document.createElement("base");
		base.href = '/';

		document.head.append(base);

		const link = document.createElement("link");

		link.rel = "stylesheet";
		link.href = "//static.cloud-clinic.ru/appointment-widget/styles.css";

		document.head.append(link);

		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetPhoneNumberResult':
					this.setState({ fetchedData: e.detail.data });

					break;
				default:
					console.log(e.detail.type);
			}
		});

		connect.send("VKWebAppGetPhoneNumber", {});
	}

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" fetchedData={this.state.fetchedData} />
			</View>
		);
	}

	static loadScript(url, type = null, noModule = false) {
		const script = document.createElement("script");

		script.src = url;
		script.async = false;
		script.noModule = noModule;

		if (type) script.type = type;

		document.body.appendChild(script);
	}
}

export default App;
