import React from 'react';
import PropTypes from 'prop-types';
import {Group, Panel, PanelHeader, Div} from '@vkontakte/vkui';

const Home = ({id, fetchedData}) => (
    <Panel id={id}>
        <PanelHeader theme={"light"}>Запись к врачу</PanelHeader>

        <Div>
            {fetchedData &&
            <cc-schedule modal="false" phone={`${fetchedData.phone_number}`}></cc-schedule>
            }
        </Div>

    </Panel>
);

Home.propTypes = {
    id: PropTypes.string.isRequired,
    fetchedData: PropTypes.shape({
        phone_number: PropTypes.string
    }),
};

export default Home;
